<template>

  <div>
    <br>
    <br>
    <br>
      <b-card bg-variant="light">
    <b-card-group deck>

      <b-card bg-variant="info" text-variant="white" header="Product Items" class="text-center">
        <h1>{{product}}</h1>
      </b-card>


      <b-card bg-variant="success" text-variant="white" header="Order" class="text-center">
        <h1 >{{order}}</h1>
      </b-card>
            <b-card bg-variant="warning" text-variant="white" header="Order Request" class="text-center">
        <h1>{{orderRequest}}</h1>
      </b-card>
          <b-card bg-variant="danger" text-variant="white" header="Ordered Product" class="text-center">
        <h1>{{orderedProduct}}</h1>
      </b-card>
    </b-card-group>
    </b-card>
  </div>



</template> 

<script>
import {api} from '../Api'
export default {
  name: 'LayoutCard',
  props: { 
    pageTitle: { type: String, default: 'Akaba'}
  },
   
  components: {

  },
  data() {
    return {
      
        product: '',
        order: '',
        orderRequest: '',
        orderedProduct: '',  
    }
  },

  computed: {
    updateTitle: {
      get () { return this.pageTitle },
      set (val) { this.$emit('update:pageTitle', val) }
    }
  },
 
    methods: {
    orderApi(){
      api().get('TotalOrder').then((response) => {
        console.log(response.data)
        this.order = response.data
      })
     },
    productApi(){
      api().get('TotalProduct').then((response) => {
        console.log(response.data)
        this.product = response.data
      })
    },
    OrderedProductApi(){
      api().get('TotalOrderedProduct').then((response) => {
        console.log(response.data)
        this.orderedProduct = response.data
      })
    },
    OrderRequestApi(){
      api().get('TotalOrderRequest').then((response) => {
        console.log(response.data)
        this.orderRequest= response.data
      })
    },


  },
 



  created () {
    this.updateTitle = 'DashBoard'
    this.productApi()
    this.orderApi()
    this.OrderedProductApi()
    this.OrderRequestApi()

  }
}
</script>

